import {_decorator, assetManager, Component, JsonAsset, loader, resources, Size} from 'cc';

import {Game_Board} from './Game_Board';

const {ccclass, property} = _decorator;

@ccclass('Main')
export class Main extends Component {
  @property(Game_Board) game_board: Game_Board;

  start() {
    this.game_board.show_grids(new Size(20, 20));

    resources.load<JsonAsset>(
        '../data/level_01', (err: Error, asset: JsonAsset) => {
          console.log(asset);
        });
  }
}
